import * as React from 'react';
import { useEffect, useState } from 'react';
import { navigate } from 'gatsby';
import Layout from '../components/Layout';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Parse from '../vendor/Parse';
import CardMedia from '@mui/material/CardMedia';
import CircularProgress from '@mui/material/CircularProgress';


const AppsPage = ({ pageContext }) => {
    const { category, apps, lastUpdated } = pageContext
  const [loading, setLoading] = useState(false);

  const handleCardClick = (path) => {
    navigate(path);
  };

  const approvedApps = apps//.filter((app) => app.status === 'approved');
  const pendingApps = apps.filter((app) => app.status === 'pending');

  const renderAppCards = (appsArray) =>
    appsArray.map((app) => {
      const randomColor = `#${Math.floor(Math.random() * 16777215).toString(16)}`;
      return (
        <Grid item key={app.id} xs={12} sm={6} md={4}>

          <Card
            sx={{
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              cursor: 'pointer',
              opacity: app.status === 'pending' ? 0.6 : 1,
              pointerEvents: app.status === 'pending' ? 'none' : 'auto',

            }}
            onClick={() => handleCardClick(`https://appseek.ai/r/${app.placeholder}`)}
          >
            {
              app?.cover && <CardMedia
                component="img"
                height="200"
                image={app?.cover}
                alt="Paella dish"
              />
            }
            {
                !app?.cover && <Box
              sx={{
                height: 200,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: randomColor,
                color: '#fff',
                fontSize: '1.5rem',
                fontWeight: 500,
                textAlign: 'center'
              }}
            >
              {app.title}
            </Box>
    }
            <CardContent sx={{ flexGrow: 1 }} style={{ textAlign: 'center' }}>
              <Typography variant="h5" component="h2" gutterBottom>
                  {app.title}
                </Typography>
            
              <Typography variant="body1" color="text.secondary">
                {app.description}
              </Typography>
              {app.status === 'pending' && (
                <Typography variant="caption" color="error">
                  Pending approval
                </Typography>
              )}
            </CardContent>
          </Card>
        </Grid>
      );
    });

  return (
    <Layout>
      <Box sx={{ maxWidth: 1200, mx: 'auto', p: 3 }}>
        <Typography variant="h3" component="h1" gutterBottom align="center">
{ category }
        </Typography>

        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
            <CircularProgress />
          </Box>
        ) : (
          <>
            {/* Approved Apps Section */}
            {approvedApps.length > 0 && (
              <>
                <Typography variant="h4" component="h2" gutterBottom>
                  Available Apps
                </Typography>
                <Grid container spacing={4} sx={{ mb: 6 }}>
                  {renderAppCards(approvedApps)}
                </Grid>
              </>
            )}

            {/* Pending Approval Section */}
            {pendingApps.length > 0 && (
              <>
                <Typography variant="h4" component="h2" gutterBottom sx={{ mt: 6 }}>
                  Pending Approval
                </Typography>
                <Grid container spacing={4}>
                  {renderAppCards(pendingApps)}
                </Grid>
              </>
            )}
          </>
        )}
      </Box>
    </Layout>
  );
};

export default AppsPage;
